import React, { ReactElement } from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import { css } from "@emotion/react"
import { GlobalStyle } from "../GlobalStyle"
import { useState, useLayoutEffect } from "react"

interface node {
  frontmatter: {
    tags: string[]
    title: string
  }
  internal: {
    content: string
  }
  html: any
}

interface AllMarkdownRemark {
  distinct: string[]
  nodes: node[]
}

const index_layout_css = css`
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`

const title_layout_css = css`
  position: absolute;
  left: 250px;
  text-align: left;
`

const title_ul_css = css`
  list-style-type: none;
`

const title_li_css = css`
  margin-top: 0.25rem;
  color: rgb(52, 58, 64);
  font-size: 0.875rem;
  line-height: 1.5;
  display: list-item;
  text-align: -webkit-match-parent;
  &.selectedTag {
    font-weight: bold;
    color: rgb(91, 0, 171);
  }
  &:hover {
    text-decoration: underline;
  }
  cursor: pointer;
  a {
    color: rgb(52, 58, 64);
    text-decoration: none;
  }
`

const markdown_content_layout_css = css`
  display: block;
  width: 50%;
  height: 100vh;
`

const markdown_block_div_css = css`
  margin: 30px 0 30px 0;
  max-height: 740px;
  padding: 4rem;
  overflow: hidden;
`

const markdown_title_css = css`
  display: block;
  margin: 0.67em 0;
  font-weight: 600;
  padding-bottom: 0.3em;
  font-size: 2em;
  border-bottom: 1px solid hsla(210, 18%, 87%, 1);
`

const markdown_content_div_css = css`
  font-size: 1rem;
  /* white-space: pre; */
  overflow-wrap: break-word;
  word-break: keep-all;
  max-height: 300px;
  /* white-space: nowrap; */
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;

  color: rgb(73, 80, 87);
  font-size: 16px;
  line-height: 24px;
`

const markdown_content_tags_div_css = css`
  margin: 10px 0 10px 0;
`

const tags_of_markdown_content_a_css = css`
  margin-bottom: 0.875rem;
  background: rgb(241, 243, 245);
  padding-left: 1rem;
  padding-right: 1rem;
  height: 2rem;
  border-radius: 1rem;
  display: inline-flex;
  -webkit-box-align: center;
  align-items: center;
  margin-right: 0.875rem;
  color: rgb(91, 0, 171);
  text-decoration: none;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
`

export default function Home(): ReactElement {
  const [allMarkdown, setAllMarkdown] = useState<AllMarkdownRemark>()
  const { allMarkdownRemark }: { allMarkdownRemark: AllMarkdownRemark } =
    useStaticQuery(tagsQuery)
  console.log(allMarkdownRemark)

  useLayoutEffect(() => {
    setAllMarkdown(allMarkdownRemark)
  }, [allMarkdownRemark])

  const showAll = (dom: HTMLLIElement) => {
    const selectedTagDOM = document.querySelector(".selectedTag")
    selectedTagDOM?.classList.remove("selectedTag")
    dom.classList.add("selectedTag")

    setAllMarkdown(allMarkdownRemark)
  }

  const onTagClick = (dom: HTMLSpanElement, tag: string) => {
    const selectedTagDOM = document.querySelector(".selectedTag")
    selectedTagDOM?.classList.remove("selectedTag")
    dom.classList.add("selectedTag")

    const newNodes = allMarkdownRemark.nodes.filter(
      node => node.frontmatter.tags.indexOf(tag) !== -1
    )

    setAllMarkdown(prev => {
      if (prev == undefined) return
      return { distinct: prev.distinct, nodes: newNodes }
    })
  }

  const removeTags = (str: string) => {
    if (str === null || str === "") return false
    else str = str.toString()

    // Regular expression to identify HTML tags in
    // the input string. Replacing the identified
    // HTML tag with a null string.

    return str.replace(/(<h1>.*<\/h1>)/i, "").replace(/(<([^>]+)>)/gi, "")
  }

  return (
    <>
      <GlobalStyle />
      <div css={index_layout_css}>
        <div css={title_layout_css}>
          <ul css={title_ul_css}>
            <li
              css={title_li_css}
              className="selectedTag"
              onClick={e => showAll(e.currentTarget)}
            >
              전체보기
            </li>
            {allMarkdownRemark.distinct.map((tag, index) => {
              return (
                <li
                  key={index}
                  css={title_li_css}
                >
                  {/* <Link to={`/${tag}`}>{tag}</Link> */}
                  <span onClick={e =>
                    onTagClick(e.currentTarget, e.currentTarget.innerText)
                  }>{tag}</span><span>({allMarkdownRemark?.nodes.filter((node, index) => node.frontmatter.tags.includes(tag)).length})</span>
                </li>
              )
            })}
          </ul>
        </div>
        <div css={markdown_content_layout_css}>
          {allMarkdown &&
            allMarkdown.nodes &&
            allMarkdown.nodes.map((node, index) => {
              const findImagePhrase: any =
                node.internal.content.match(/!\[.*\]\(.*\)/i)
              let firstImagePhrase: any = ""
              if (findImagePhrase !== null)
                firstImagePhrase = findImagePhrase[0]
              if (firstImagePhrase.match(/\(.*\)/i) !== null) {
                firstImagePhrase = firstImagePhrase.match(/\(.*\)/i)[0]
                firstImagePhrase = firstImagePhrase.substring(
                  1,
                  firstImagePhrase.length - 1
                )
              }
              // console.log(firstImagePhrase)

              node.html = removeTags(node.html)
              return (
                <div css={markdown_block_div_css} key={index}>
                  <Link to={`/${node.frontmatter.title}`}>
                    <h1 css={markdown_title_css}>{node.frontmatter.title}</h1>
                    <img
                      src={
                        firstImagePhrase == "" ? "./test.png" : firstImagePhrase
                      }
                      alt="can not find image"
                    />
                    <p css={markdown_content_div_css}>{node.html}</p>
                  </Link>
                  <div css={markdown_content_tags_div_css}>
                    {node.frontmatter.tags.map((tag, nodeIndex) => {
                      return (
                        <a css={tags_of_markdown_content_a_css} key={nodeIndex}>
                          {tag}
                        </a>
                      )
                    })}
                  </div>
                  <hr />
                </div>
              )
            })}
        </div>
      </div>
    </>
  )
}

export const tagsQuery = graphql`
  query MyQuery {
    allMarkdownRemark(sort: { fields: frontmatter___tags, order: DESC } filter: {frontmatter: {tags: {nin: "temp"}}}) {
      nodes {
        frontmatter {
          tags
          title
        }
        internal {
          content
        }
        html
      }
      distinct(field: frontmatter___tags)
    }
  }
`
